@import url(https://fonts.googleapis.com/css?family=Dancing+Script);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  height: 100vh;
  width: 100vw;
  background-image: url(/ytgrabber/static/media/bg.5edfd059.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: white; }

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; }

.appTitle {
  font-family: 'Dancing Script', cursive;
  margin: 100px 0; }
  .appTitle a {
    text-decoration: none;
    color: white; }
  .appTitle .appName {
    color: white;
    text-decoration: none;
    font-size: 4em; }

.Form {
  margin-bottom: 100px;
  color: black; }
  .Form .Input {
    padding: 15px 20px;
    font-size: 1em;
    width: 40vw;
    border-radius: 5px;
    border: 0; }
  .Form .Button {
    font-size: 1.5em;
    padding: 10px 20px;
    border-radius: 5px;
    border: 0;
    background-color: black;
    color: white; }

.results-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }
  .results-container .qualHeader {
    background-color: white;
    color: black;
    padding: 5px 10px;
    border-radius: 2px; }
  .results-container .img-container {
    width: 300px;
    margin: 20px 30px;
    height: 175px;
    object-fit: cover; }

@media (max-width: 1024px) {
  .Form {
    margin-top: 30px;
    margin-bottom: 10px; }
  .results-container {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 50px 0 0; } }

